<template>
    <div id="imgCropper" class="m-Upload">
        <!--  -->
        <div v-if="getImageUrl != ''" class="upload-show">
            <div class="upload-img-wrap"><img class="upload-image" :src="getImageUrl" alt="" @error="notFoundPic"/></div>
            <!--  -->
            <div class="upload-mask">
                <Icon v-if="preview" type="md-eye" class="g-mr10" @click.native="viewImage"></Icon>
                <Icon type="md-create" @click.native="uploadClick"></Icon>
            </div>
        </div>
        <div class="clearfix"></div>
        <!--  -->
        <div v-show="showUploadBtn" class="upload-btn" @click="uploadClick">
            <Icon type="md-camera" size="20"></Icon>
            <input ref="imgFile" type="file" :accept="format" hidden @change="selectFile"/>
        </div>
        <div class="clearfix"></div>
        <!--   -->
        <template v-if="uploadStatus != 'finished'">
            <Progress v-if="showProgress" :percent="percentage" hide-info :stroke-width="3" style="width:340px;"></Progress>
        </template>
        <br/>
        <!--  -->
        <span class="hint">
            * jpgjpegpng
            <span v-if="sizeHint"> {{ sizeHint }}</span>
            
            <span v-if="fileSize < 1024">{{ fileSize }}kb</span>
            <span v-else>{{ Math.floor(fileSize/1024) }}M</span>
            
        </span>
        <!--  -->
        <Modal :title="cropImgModel.title" class="m-cropper-img" v-model="cropImgModel.show" width="900" @on-cancel="hideCropModel">
            <!--  -->
            <div class="img-cropper-area">
                <vueCropper
                    ref="cropper"
                    :img="option.img"
                    :outputSize="option.size"
                    :outputType="option.outputType"
                    :info="true"
                    :canScale="option.canScale"
                    :autoCrop="option.autoCrop"
                    :autoCropWidth="autoCropWidth"
                    :autoCropHeight="autoCropHeight"
                    :fixed="fixed"
                    :fixedNumber="fixedNumber"
                    @realTime="realTime"
                ></vueCropper>
            </div>
            <!--  -->
            <div class="img-preview-area">
                <div class="preview" :style="previewStyle">
                    <div v-if="previews" :style="previews.div">
                        <img :src="previews.url" :style="previews.img">
                    </div>
                    <span v-else></span>
                </div>
            </div>
            <div class="clearfix"></div>
            <!--  -->
            <div class="img-button-area">
                <Button type="primary" @click="changeScale(1)"><Icon type="md-add"></Icon></Button>
                <Button type="primary" @click="changeScale(-1)"><Icon type="md-remove"></Icon></Button>
                <Button type="primary" @click="rotateRight"><Icon type="md-refresh"></Icon></Button>
                <!-- <Button type="primary" @click="previewImg('blob')"></Button> -->
            </div>
            <!--  -->
            <div style="margin-top:10px">
                <span class="hint">
                    * jpgjpegpng
                    <span v-if="sizeHint"> {{ sizeHint }}</span>
                    
                    <span v-if="fileSize < 1024">{{ fileSize }}kb</span>
                    <span v-else>{{ Math.floor(fileSize/1024) }}M</span>
                    
                </span>
            </div>
            <!--  -->
            <div slot="footer">
                <Button type="default" @click="hideCropModel"></Button>
                <Button type="primary" @click="comfirCropper"></Button>
            </div>
        </Modal>
        <!--  -->
        <Modal title="" class="m-view-img" v-model="showModal" width="600">
            <img :src="getImageUrl" @error="notFoundPic"/>
            <div slot="footer"></div>
        </Modal>
    </div>
</template>

<script>
    // 
    import vueCropper from 'vue-cropper'
    // JS
    import Common from 'common/common.js'
    // 
    import { UploadImg } from 'mixins'

    export default {
        name: 'imgCropper',
        mixins: [ UploadImg ],
        components: { vueCropper },
        // 
        props: {
            // Url
            src:{
                type: String,
                default: ''
            },
            // 
            preview:{
                type: Boolean,
                default: false
            },
            // 
            sizeHint:{
                type: String,
                default: '100*100px'
            },
            // KB
            fileSize:{
              type: Number,
              default: 150
            },
            // 
            autoCropWidth:{
                type: Number,
                default: 100
            },
            // 
            autoCropHeight:{
                type: Number,
                default: 100
            },
            // 
            fixed:{
                type: Boolean,
                default: false
            },
            // 
            fixedNumber: {
                type: Array,
                default: () => [4, 3]
            },
        },
        data () {
            return {
                // 
                cropImgModel: {
                    title: '',
                    show: false
                },
                // https://github.com/xyxiao001/vue-cropper
                option: {
                    img: '',
                    size: 1,
                    outputType: 'jpg',
                    canScale: false,
                    autoCrop: true
                },
                // 
                previews: {},
                // 
                previewStyle: {},
                // url
                cropImgUrl: '',
                // 
                getImageUrl: ''
            }
        },
        watch: {
			src(val) {
                if(val){
                    this.showUploadBtn = false;
                    this.getImageUrl = this.src;
                }
			}
        },
        created() {
            if(this.src){
                this.showUploadBtn = false;
                this.getImageUrl = this.src;
            }
        },
        methods: {
            // 
            uploadClick(){
                // 
                this.$refs.imgFile.click();
            },
            // 
            comfirCropper(){
                this.progressShow();

                // 
                let progress = setInterval(() => {
                    if(this.percentage == 90) clearInterval(progress);
                    this.percentage += 10;
                },100);

                const fileList = this.$refs.imgFile.files;

                // 
                this.$refs.cropper.getCropBlob((data) => {

                    data.lastModifiedDate = new Date();

                    var thisFile = Bmob.File(fileList[0].name, data);
                    this.uploadToBomb(thisFile).then(res => {
                        this.getImageUrl = res[0].url;
                        // url
                        this.$emit('get-img-url', this.getImageUrl);
                        this.hideCropModel();
                    }).catch(err => {
                        console.log(err);
                        this.hideCropModel();
                    });
				})
            },
            // 
            hideCropModel(){
                this.cropImgModel.show = false;
                this.$refs.cropper.clearCrop();
                this.option.img = '';
                this.cropImgUrl = '';
            },
            // 
            selectFile(){
                // 
                const fileList = this.$refs.imgFile.files;

                if(fileList.length > 0){
                    let fileName = fileList[0].name;
                    // kb
                    let fileSize = Math.floor(fileList[0].size / 1024);

                    let maxSize = this.fileSize;
                    let overHint = maxSize + 'kb';
                    // 
                    if(fileSize > maxSize){
                        if(maxSize > 1024) overHint = Math.floor(maxSize/1024) + 'M';
                        this.$Notice.warning({
                            title: '',
                            desc: ' ' + fileName + '  ' + overHint
                        });
                        this.progressHide();
                        return false;
                    }

                    // 
                    this.cropImgModel.show = true;

                    let file = fileList[0];
                    // FileReader API base64  
                    let reader = new FileReader();
                    reader.onload = (e) => {
                        let data;
                        if (typeof e.target.result === 'object') {
                            // Array Bufferblob base64
                            data = window.URL.createObjectURL(new Blob([e.target.result]));
                        }
                        else {
                            data = e.target.result;
                        }

                        this.option.img = data;

                        // 
                        let cropperArea = document.querySelector('.img-cropper-area');
                        cropperArea.style.height = '500px';
                        // $('.img-cropper-area').height('500px');
                    }
                    // base64
                    // reader.readAsDataURL(file);
                    // blob
                    reader.readAsArrayBuffer(file);
                }
                else console.log('');
            },
            // 
            changeScale(num){
                this.$refs.cropper.changeScale(num);
            },
            // 
            rotateRight(){
                this.$refs.cropper.rotateRight();
            },
            // 
            realTime (data) {
                this.previews = data;
                // 
                this.previewStyle = {
                    width: data.w + "px",
                    height: data.h + "px",
                    overflow: "hidden",
                    margin: "0 auto",
                    zoom: 0.6
                };
            },
            // 
            previewImg (type) {
                // 
                if (type === 'blob') {
                    this.$refs.cropper.getCropBlob((data) => {
                        this.cropImgUrl = window.URL.createObjectURL(data);
                    })
                }
                else {
                    this.$refs.cropper.getCropData((data) => {
                        this.cropImgUrl = data;
                    })
                }

                // 
                // let test = window.open('about:blank');
			    // test.document.body.innerHTML = '..';
                // if (type === 'blob') {
                //     this.$refs.cropper.getCropBlob((data) => {
                //         test = window.open('');
                //         test.location.href = window.URL.createObjectURL(data);
                //     })
                // }
                // else {
                //     this.$refs.cropper.getCropData((data) => {
                //         test.location.href = data;
                //     })
                // }
            },
        }
    }
</script>

<style lang="less" scoped>
    @import "../../assets/less/upload_img";

    .img-cropper-area{
        width: 500px;
        float: left;
        margin-right: 35px;
    }

    .img-preview-area{
        width: 330px;
        min-height: 100px;
        line-height: 100px;
        text-align: center;
        overflow: hidden;
        float: left;

        img{
            width: 100%;
            display: block;
        }

        .preview{
            width: 100%;
            border: 1px dashed #e9eaec;
        }
    }

    .img-button-area{
        margin-top: 10px;

        button{
            margin-right: 10px;
        }
    }

    .upload-show{
        margin-bottom: 0;
    }
</style>
